html, body, #root {
  height: 100%;
  overflow: hidden;
}

#app {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#app-bar {
  display: inline-block;
  padding: 0 8px;

  z-index: 1300;
  background-color: #FFF;
}

#function-plot {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background: #00F;
  overflow: hidden;
}

#function-plot > canvas {
  width: 100%;
  height: 100%;
}

#function-input {
  margin-left: 3px;
  margin-right: 75px;

  overflow: hidden;
  -ms-flex-positive: 1;
      flex-grow: 1
}

#help-button {
  position: absolute;
  right: 0;
}

#help-text {
  padding: 30px;
  width: 500px;
}

#help-text > p {
  font-size: 14px;
  line-height: 2;
  text-align: justify;
  text-justify: inter-word;
}

.control-panel {
  width: 300px;
}

div.drawer-content {
  overflow-x: hidden;
}

div.variable-slider {
  width: 90%;
}

@media (max-width: 900px) {
  #help-text {
    width: calc(100% - 60px);
  }
}

@media (max-width: 600px) {
  #help-text {
    width: calc(100% - 40px);
    padding: 20px;
  }
}

@media (max-width: 480px) {
  #help-text > p {
    text-align: left;
  }

  .control-panel {
    width: 100%;
  }
}



